.Backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

.Modal {
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 200px;
    max-width: 300px;
    width: 100%;
    padding: 12px;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.contactsBtn {
    border-radius: 10px;
    border: 1px solid rgb(73, 73, 73);
    display: inline-block;
    cursor: pointer;
    color: rgb(73, 73, 73);
    font-family: Arial;
    font-size: 12px;
    font-weight: bold;
    padding: 3px 15px;
}
.contactsBtn:hover {
    color: #00b0e7;
    border: 1px solid #00b0e7;
}
.closeBtn {
    position: absolute;
    top: 8px;
    right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
}
.closeSvg:hover {
    filter: invert(0.5);
}
