.wrapTabs {
    display: flex;
    width: 60%;
}

.label {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(128, 128, 128, 0.541);
    border-left: 1px solid rgba(128, 128, 128, 0.541);
    border-right: 1px solid rgba(128, 128, 128, 0.541);
    padding: 2% 2%;
    margin-right: 2%;
    cursor: pointer;
    color: rgb(73, 73, 73);
    margin: 0;
}

.radioBtn {
    display: none;
}

.text {
    margin: 0;
    padding: 0 3%;
}

.tabContent {
    border: 1px solid rgba(128, 128, 128, 0.541);
    margin: 0;
}
.tabContentLink {
    color: black;
}

.tabContentLink:hover {
    color: #00b0e7;
}

.radioBtn:checked + .label {
    background-color: white;
    color: #00b0e7;
    position: relative;
    top: -0.08rem;
}

@media (min-width: 1200px) {
    .wrapTabs {
        display: flex;
        justify-content: space-between;
        width: 60%;
    }
}
