body {
    padding: 0;
    margin: 0 auto;
    max-width: 1200px;
    display: flex;
    background: linear-gradient(to top, #00336661 0%, #ffffff 100%);
}
header {
    background-color: white;
}

header,
main,
footer {
    background-color: rgba(255, 255, 255, 0.897);
}

a {
    color: #00b0e7;
}

ul {
    margin: 0.7% auto;
    color: rgb(73, 73, 73);
}
.wrapHeader {
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    margin: 0 auto;
    padding: 0 2%;
    height: 4rem;
}

.wrapMain {
    margin: 0 auto;
    padding: 1% 2% 1% 2%;
}
.wrapPersonalData {
    width: 100%;
}
.wrapSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.wrapFooter {
    margin: 0 auto;
    padding: 1% 2% 1% 2%;
    border-top: 3px solid rgba(128, 128, 128, 0.541);
    text-align: center;
}

.downloadLink {
    color: red;
    font-weight: 600;
}

.footerSection {
    margin: 0;
    display: flex;
    justify-content: center;
}

@media (min-width: 1200px) {
    .wrapHeader {
        transform: scale(1);
    }
    .wrapSection {
        display: flex;
        flex-direction: row;
    }
    .section {
        width: 60%;
    }
    .wrapAside {
        width: 40%;
        padding: 0 2%;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .wrapHeader {
        transform: scale(0.9);
    }
    .wrapSection {
        display: flex;
        flex-direction: row;
    }
    .section {
        width: 60%;
    }
    .wrapAside {
        width: 40%;
        padding: 0 2%;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .wrapHeader {
        transform: scale(0.8);
    }
}
@media (max-width: 767px) {
    .wrapHeader {
        transform: scale(0.7);
    }
}
@media (max-width: 480px) {
    .wrapHeader {
        transform: scale(0.6);
    }
}
@media (max-width: 320px) {
    .wrapHeader {
        transform: scale(0.5);
    }
}
